import React, { Component } from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faExternalLinkSquare } from "@fortawesome/pro-duotone-svg-icons/faExternalLinkSquare"
import { Badge } from "reactstrap"

export default class ProjectCurriculum extends Component {

  render(name, link, description, icon, year){

    function desc(description){
      if(description)
        return (
          <span>
            &nbsp;
            <span className="small text-muted">{String(description)}</span>
          </span>
        )
      else
        return ("")
    }

    function projectName(name, color){
      if(color === "primary")
        return (
          <strong>{name}</strong>
        )
      else
        return (<span>{name}</span>)
    }

    return (
      <div className="m-1">
        <a className={this.props.link?"link text-" + this.props.color:"text-" + this.props.color} href={this.props.link} rel="noopener noreferrer" target="_blank">
          <FontAwesomeIcon icon={this.props.icon} className={"text-" + this.props.color} fixedWidth />
          &nbsp;&nbsp;
          { projectName(String(this.props.name), this.props.color) }
        </a>
        {desc(this.props.description)}
      </div>
    )
  }

}
